<template>
	<!--  -->
	<div class="z-video">

		<!-- header -->
		<header class="mt20">

			<span class="demonstration"></span>
			<el-date-picker class="ml20 mr20" value-format="yyyy-MM-dd HH:mm:ss" v-model="value1" type="datetimerange" :picker-options="pickerOptions" range-separator=""
				start-placeholder="" end-placeholder=""></el-date-picker>

			<span class="demonstration mr20"></span>
			<el-select v-model="file_class" filterable placeholder="">
				<el-option label="" :value="''"></el-option>
				<el-option v-for="item in video_type" :key="item.id" :label="item.name" :value="item.id">
				</el-option>
			</el-select>

			<span class="demonstration mr20 ml20"></span>
			<el-select v-model="state" placeholder="">
				<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
				</el-option>
			</el-select>

			<el-button @click="onQuery" type="primary" class="ml20"></el-button>
			<el-button @click="addView" class="ml20" type="success"></el-button>

			<el-tag class="ml20">{{total}}</el-tag>
		</header>

		<el-table ref="table" :data="tableData" stripe highlight-current-row style="width: 100%;margin-top:20px">
			<el-table-column type="index" :index="indexMethod"></el-table-column>
			<el-table-column prop="title" label="" width="120">
				<template slot-scope="scope" class="s">
					<div class="cover">
						<i @click="playView(scope.$index, tableData)" class="el-icon-video-play"></i>
						<img :src="scope.row.coverBase" />
					</div>
				</template>
			</el-table-column>
			<el-table-column prop="title" label="" width="230"></el-table-column>
			<el-table-column prop="introduction" label=""></el-table-column>
			<el-table-column prop="releaseTime" label="" width="180" sortable></el-table-column>
			<el-table-column prop="volume" label="()" width="110" align="center" sortable></el-table-column>
			<el-table-column prop="type" label="" width="120">
				<template slot-scope="scope">
					<span>{{scope.row.type | VideoType}}</span>
				</template>
			</el-table-column>
			<el-table-column prop="shelf" label="" width="80">
				<template slot-scope="scope">
					<span v-if="scope.row.shelf == '1' " style="color:#67C23A"></span>
					<span v-else style="color:red"></span>
				</template>
			</el-table-column>

			<el-table-column fixed="right" label="" width="180">
				<template slot-scope="scope">

					<el-button v-if="scope.row.shelf == '0'" @click.native.prevent="Enable(scope.$index, tableData, 1)" type="text" size="small">
						
					</el-button>

					<el-button v-if="scope.row.shelf == '1'" @click.native.prevent="Enable(scope.$index, tableData, 0)" type="text" size="small">
						
					</el-button>

					<el-button @click.native.prevent="editUser(scope.$index, tableData)" type="text" size="small">
						
					</el-button>

					<el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small">
						
					</el-button>

				</template>
			</el-table-column>

		</el-table>

		<div class="block mt20 tr">
			<el-pagination @current-change="changeCurr" layout="prev, pager, next" :total="total"></el-pagination>
		</div>

		<el-dialog title="" destroy-on-close :visible.sync="dialogVisible" width="60%" top="10vh">
			<video-player class="video-player vjs-custom-skin" ref="videoPlayer" :playsinline="true" :options="playerOptions"></video-player>
		</el-dialog>

	</div>
</template>

<script>
export default {
	data() {
		const that = this;
		return {
			pickerOptions: {
				shortcuts: [{
					text: '',
					onClick(picker) {
						const end = new Date();
						const start = new Date();
						start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
						picker.$emit('pick', [start, end]);
					}
				}, {
					text: '',
					onClick(picker) {
						const end = new Date();
						const start = new Date();
						start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
						picker.$emit('pick', [start, end]);
					}
				}, {
					text: '',
					onClick(picker) {
						const end = new Date();
						const start = new Date();
						start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
						picker.$emit('pick', [start, end]);
					}
				}]
			},
			playerOptions: {
				playbackRates: [0.7, 1.0, 1.5, 2.0], //
				autoplay: false, //true,
				muted: false, // 
				loop: false, // 
				preload: 'auto', // <video>auto,
				language: 'zh-CN',
				aspectRatio: '16:9', //  - "16:9""4:3"
				fluid: true, // trueVideo.js player
				sources: [{
					type: "",//git
					src: "" //url
				}],
				poster: "", //
				// width: document.documentElement.clientWidth, //
				notSupportedMessage: '', //Video.js
				controlBar: {
					timeDivider: true,
					durationDisplay: true,
					remainingTimeDisplay: false,
					fullscreenToggle: true  //
				}
			},
			dialogVisible: false,
			dialogVisible1: false,
			options: [{
				value: "",
				label: '',
			}, {
				value: "1",
				label: '',
			}, {
				value: "0",
				label: '',
			}],
			state: '',
			value1: [],
			tableData: [],
			total: 0,
			video_type: [],
			file_class: ''
		}
	},
	mounted() {
		window.that = this
	},
	created() {
		this.getViewList();
		this.getAllvideoType()
	},
	filters: {
		VideoType(type) {


			let typeNmae = ''

			for (let i in that.video_type) {
				if (type == that.video_type[i].id) {
					typeNmae = that.video_type[i].name;
				}
			}

			return typeNmae == '' ? '' : typeNmae
		}
	},
	methods: {

		/// &&
		Enable(index, rows, states) {
			console.log(rows[index]);
			console.log(states);
			this.post('/view/isenable', {
				id: rows[index].id,
				states: states
			}).then(res => {
				console.log(res);
				this.tableData[index].shelf = states;
			})
		},

		async getAllvideoType() {
			let res = await this.get('videoType/getAllvideoType');
			console.log(res);
			if (res) {
				let data = res.data;
				this.video_type = data;
			}
		},
		// 
		onQuery() {
			this.getViewList();
		},
		// 
		addView() {
			this.hf('/home/addview')
		},
		// 
		editUser(index, rows) {
            console.log(rows[index]);
            this.hf('/home/addview', rows[index].id);
		},
		// 
		deleteRow(index, rows) {
			console.log(rows[index]);
			this.$confirm(', ?', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning'
			}).then(() => {

				this.delete('/view/deleteVideo/' + rows[index].id,{
                    file_type:rows[index].file_type,
                    viewFileUrl:rows[index].viewFileUrl.split('/')[2]
                }).then(res => {
					if (res.code == 200) {
						this.$message({
							type: 'success',
							message: '!'
						});
						this.getViewList()
					}
				})

			})
		},
		// 
		changeCurr(e) {
			console.log(e);
			this.page = e;
			this.getViewList()
		},
		// 
		playView(index, rows) {
			console.log(rows[index]);
			let data = rows[index];

			if (data.file_type == 1) {
				this.playerOptions.sources[0].src = data.viewFileUrl;
				this.playerOptions.poster = data.coverBase

			} else {
				this.playerOptions.sources[0].src = this.http + data.viewFileUrl;
				this.playerOptions.poster = data.coverBase

			}

			console.log(this.playerOptions.sources[0].src);

			this.dialogVisible = true;
		},
		indexMethod(index) {
			return index + 1;
		},
		// 
		async getViewList() {

			let startTiem, endTiem = '';
			if (this.value1 != null) {
				startTiem = this.value1[0];
				endTiem = this.value1[1];
			} else {
				startTiem, endTiem = ''
			}

			let res = await this.get('/view/getViewList', {
				page: this.page,
				count: 10,
				state: this.state,
				startTiem,
				endTiem,
				type: this.file_class
			})

			if (res) {
				console.log(res);
				let data = res.data;

				this.tableData = data;
				this.total = res.total
			}
		}
	}
}
</script>

<style lang="less" scoped>
.z-video {
	.cover {
		height: 50px;
		position: relative;

		i {
			position: absolute;
			font-size: 20px;
			color: #fff;
			top: 18px;
			z-index: 9;
			left: 32%;
			cursor: pointer;
			transition: all 0.1s ease-out;

			&:hover {
				transform: scale(1.1);
			}
		}

		img {
			height: 100%;
		}
	}
}
</style>