// ,qselement

import axios from "axios";
import qs from "qs";
import app from '../main'
import { Message, Loading, Col } from 'element-ui'; // ui
let loadingInstance = null

const server = {
    pro: "https://doctor.mipmci.com/zuul/api/", //
    dev: "http://39.100.122.13:3000/api/", //
    util: "http://127.0.0.1:3000/api/", //
    // util: "http://39.100.122.13:3000/" //
};

const origin = location.hostname;

let DOMIN = "";

for (let i in server) {
    let item = server[i];
    if (item.indexOf(origin) > -1) {
        DOMIN = item;
    }
}

if (!DOMIN) DOMIN = server.util ? server.util : server.dev;

/*
  axios 
 */
const service = axios.create({
    baseURL: DOMIN,
    timeout: 5000 // 
});

/*
  request 
 */
service.interceptors.request.use(config => {

    // loadingInstance = Loading.service({
    //     lock: true,
    //     text: 'loading...',
    //     background: 'rgba(255, 255, 255)'
    // })


    let header = "application/x-www-form-urlencoded";
    let objData = JSON.stringify(config.headers['Content-Type']) == JSON.stringify(header) ?
        qs.stringify({...config.data }) : {...config.data };

    config.method === 'post' ?
        config.data = objData :
        config.params = {...config.params };

    if (app.getItem("token")) config.headers["authorization"] = 'Bearer ' + app.getItem("token");

    return config;

}, error => { //

    // loadingInstance.close()


    Message('')
    Promise.reject(error)
});

/* 
 respone 
*/
service.interceptors.response.use(
    response => {
        //
        let code = response.data.code;
        // loadingInstance.close()

        console.log(code);
        if (code == "200") {
            return response.data;
        } else {
            app.$message.error(response.data.msg);

            return false;
        }
    },
    error => {
        //

        // loadingInstance.close()

        console.log(error);

        console.log("error");


        app.hf('/login')
        Message("");

        return;
        // return Promise.reject(error)
    }
);

export default {
    service,
    DOMIN
};