<!-- video_type -->
<template>
	<section class='video_type'>

		<header class="mt20">
			<el-button @click="addView" class="ml20" type="success"></el-button>
		</header>

		<el-table ref="table" :data="tableData" max-height="800px" stripe highlight-current-row style="width: 100%;margin-top:20px">
			<el-table-column type="index" :index="indexMethod"></el-table-column>
			<el-table-column prop="name" label="" width="300"></el-table-column>
			<el-table-column prop="establishTime" label=""></el-table-column>

			<el-table-column fixed="right" label="" width="180">
				<template slot-scope="scope">

					<el-button @click.native.prevent="editUser(scope.$index, tableData)" type="text" size="small">
						
					</el-button>

					<el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small">
						
					</el-button>

				</template>
			</el-table-column>

		</el-table>

		<el-dialog @close="ruleForm.name = ''" :title="title_index == 0?'':''" destroy-on-close :visible.sync="dialogVisible" width="30%" top="10vh">
			<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

				<el-form-item label="" prop="name">
					<el-input v-model="ruleForm.name"></el-input>
				</el-form-item>

				<el-form-item class="mt20">
					<el-button type="primary" @click="submitForm('ruleForm')"></el-button>
					<el-button @click="dialogVisible = false"></el-button>
				</el-form-item>

			</el-form>
		</el-dialog>
	</section>
</template>
   
<script>
export default {
	data() {
		return {
			tableData: [],
			dialogVisible: false,
			ruleForm: {
				name: ''
			},
			rules: {
				name: [
					{ required: true, message: '', trigger: 'blur' },
					{ min: 2, max: 6, message: ' 2  6 ', trigger: 'blur' }
				],
			},
			title_index: 0
		};
	},

	created() {

	},

	mounted() {
		this.getAllvideoType()
	},

	methods: {
		// 
		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {

					if (this.title_index == 0) {
						// 
						this.post('videoType/addVideoType', {
							name: this.ruleForm.name
						}).then(res => {
							if (res) {
								this.$message({
									message: ` ${this.ruleForm.name} `,
									type: 'success'
								});

								this.ruleForm.name = '';
								this.dialogVisible = false;
								this.getAllvideoType()
							}
						})
					} else {
						// 
						this.post('videoType/editVideoType', {
							name: this.ruleForm.name,
							id: this.title_index
						}).then(res => {
							if (res) {
								this.$message({
									message: ` ${this.ruleForm.name} `,
									type: 'success'
								});

								this.ruleForm.name = '';
								this.dialogVisible = false;
								this.getAllvideoType()
							}
						})


					}
				} else {
					console.log('error submit!!');
					return false;
				}
			});
		},
		indexMethod(index) {
			return index + 1;
		},
		// 
		async getAllvideoType() {
			let res = await this.get('videoType/getAllvideoType');
			console.log(res);
			if (res) {
				let data = res.data;
				this.tableData = data;
			}
		},
		// 
		editUser(index, rows) {
			console.log(rows[index]);
			this.title_index = rows[index].id;
			this.dialogVisible = true;
			this.ruleForm.name = rows[index].name
		},

		// 
		deleteRow(index, rows) {
			console.log(rows[index]);
			this.$confirm(', ?', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning'
			}).then(() => {
				this.delete('videoType/deleteVideoType/' + rows[index].id).then(res => {
					this.$message({
						message: ` ${rows[index].name}`,
						type: 'success'
					});

					this.getAllvideoType()
				})
			})


		},

		// 
		addView() {
			this.title_index = 0;
			this.dialogVisible = true;
		}
	},
	updated() {

	}
};
</script>
   
<style lang='less' scoped>
.video_type {
}
</style>